<template>
  <div class="publicity">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="form">
        <el-form-item label="封面" prop="bannerImage">
          <span class="sign">1125*450</span>
          <img class="upload-img" v-show="form.bannerImage" :src="form.bannerImage + '?imageMogr2/crop/375x150'"
               alt="图片加载失败">
          <img class="remove-img" v-show="form.bannerImage" @click="removeCover()" src="../../../assets/img/资源27@2x.png"
               alt="">
          <label for="cover">
            <div class="cover-img">
              <img class="loading" v-show="!form.bannerImage" src="../../../assets/img/资源28@2x.png" alt="">
            </div>
            <input accept='image/*' @change="uploadCover($event)" ref="uploadCover" id="cover" type="file" hidden>
          </label>

        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input class="input" v-model="form.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="正文内容" prop="content">
          <wang-editor v-if="form.content || !editStatus" :content.sync="form.content"/>
        </el-form-item>
        <el-form-item label="视频">
          <div class="upload_view">
            <div class="upload_view_list">
            </div>
            <div class="upload_view_button">
              <uploader
                      ref="uploadZone"
                      :options="uploadOptions"
                      :file-status-text="fileStatusText"
                      class="upload_view_btn">
                <uploader-unsupport></uploader-unsupport>
                <uploader-btn :attrs="attrs">上传文件</uploader-btn>
                <div v-for="(item,key) in form.attachmentList" class="item">
                  <span>视频{{key + 1}}</span>
                  <i @click="removeEnd(key)" class="el-icon-close"></i>
                </div>
                <uploader-list></uploader-list>
              </uploader>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <div class="btns">
        <div @click="doSubmit()" class="btn-submit mr20">确定</div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data () {
      // 验证正文内容
      var checkContent = (rule, value, callback) => {
        if (!value || value === '<p><br></p>') {
          callback(new Error('请输入正文内容'))
        } else {
          callback()
        }
      }
      return {
        editStatus: this.$route.params.id !== '0',
        nav: [{name: '招生资讯', path: 'go(-1)'}, {name: '设置宣传页', path: ''}],
        form: {
          bannerImage: '',
          title: '',
          content: '',
          attachmentList: [],
        },
        // 附件列表
        attachmentList: [],
        attrs: {
          accept: 'video/*'
        },
        rules: {
          bannerImage: {required: true, message: '请上传封面图', trigger: 'change'},
          title: {required: true, message: '请输入标题', trigger: 'blur'},
          content: {required: true, validator: checkContent, trigger: 'change'},
        },
        uploadOptions: {
          target: `${window.location.origin}${this.$conf.API_PATH}/uploadFileTrunk/trunkUpload.do`,
          testChunks: false,
          chunkSize: 2 * 1024 * 1024,
          singleFile: false,
          parseTimeRemaining: (timeRemaining, parsedTimeRemaining) => {
            return parsedTimeRemaining
              .replace(/\syears?/, '年')
              .replace(/\days?/, '天')
              .replace(/\shours?/, '小时')
              .replace(/\sminutes?/, '分钟')
              .replace(/\sseconds?/, '秒')
          }
        },
        // 修改分片上传文字
        fileStatusText: {
          success: '上传成功',
          error: '上传错误',
          uploading: '正在上传',
          paused: '已暂停',
          waiting: '等待中'
        },
      }
    },
    watch: {
      'form.content': function () {
        setTimeout(() => {
          this.$refs.form.validate()
        }, 1)
      }
    },
    mounted () {
      this.$nextTick(() => {
        let uploadZone = this.$refs.uploadZone
        if (uploadZone) {
          const uploader = uploadZone.uploader
          uploader.on('fileSuccess', async (rootFile, file, message) => {
            let res = await this.$ajax({
              url: `/uploadFileTrunk/mergeChunks.do`,
              query: {
                fileMd5: file.uniqueIdentifier,
                fileName: file.name
              }
            })
            this.attachmentList.push({
              name: file.name,
              url: res
            })
          })
          uploader.on('fileRemoved', async (file) => {
            this.attachmentList.some((e, i) => {
              if (e.name === file.name) {
                this.attachmentList.splice(i, 1)
                return true
              }
            })
          })
        }
      })
      if (this.editStatus) {
        this.getDetail()
      }
    },
    methods: {
      // 删除已经上传的视频
      removeEnd (key) {
        this.form.attachmentList.splice(key, 1)
      },
      // 获取页面内容
      async getDetail () {
        this.form = await this.$ajax({
          url: '/rnotice/getNotice.do',
          query: {
            noticeId: this.$route.params.id
          }
        })
      },
      // 提交
      async doSubmit () {
        await this.$refs.form.validate()
        if (this.attachmentList.length) {
          this.attachmentList.forEach(e => {
            this.form.attachmentList.push(e.url)
          })
        }
        let url = '/rnotice/addNoticeBanner.do'
        let text = '新增成功'
        if (this.editStatus) {
          url = '/rnotice/updateBannerNotice.do'
          text = '修改成功'
          this.form.noticeId = this.form.id
        }
        await this.$ajax({
          url,
          el: '.btn-submit',
          noText: true,
          query: this.form,
        })
        this.$remind(text)
        this.$router.push({
          path: '/recruit/information',
          query: {
            noticeType: 'BANNER'
          }
        })
      },
      // 上传封面图
      async uploadCover (e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        if (file.size > 1024 * 1024 * 10) {
          this.$message.warning('封面图不能超过10M')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/oss/upload.do',
          query: formData,
          el: '.cover-img',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        this.form.bannerImage = res
        this.$refs.uploadCover.value = null
      },
      // 移除封面图
      removeCover () {
        this.form.bannerImage = null
      }
    },
  }
</script>
